* {
  margin: 0;
  padding: 0;
}

#app {
  background: repeating-linear-gradient(
      190deg,
      rgba(122, 122, 122, 0.5) 40px,
      rgba(133, 133, 133, 0.5) 80px,
      rgba(163, 163, 163, 0.5) 120px,
      rgba(217, 128, 255, 0.5) 160px,
      rgba(173, 173, 173, 0.5) 200px,
      rgba(173, 173, 173, 0.5) 240px,
      rgba(163, 163, 163, 0.5) 280px,
      rgba(122, 122, 122, 0.5) 300px
    ),
    repeating-linear-gradient(
      -190deg,
      rgba(122, 122, 122, 0.5) 30px,
      rgba(133, 133, 133, 0.5) 60px,
      rgba(163, 163, 163, 0.5) 90px,
      rgba(133, 133, 133, 0.5) 120px,
      rgba(173, 173, 173, 0.5) 150px,
      rgba(78, 255, 161, 0.5) 180px,
      rgba(133, 133, 133, 0.5) 210px,
      rgba(122, 122, 122, 0.5) 230px
    ),
    repeating-linear-gradient(
      23deg,
      rgba(122, 122, 122, 0.5) 50px,
      rgba(122, 122, 122, 0.5) 100px,
      rgba(122, 122, 122, 0.5) 150px,
      rgba(122, 122, 122, 0.5) 200px,
      rgba(165, 255, 150, 0.5) 250px,
      rgba(122, 122, 122, 0.5) 300px,
      rgba(122, 122, 122, 0.5) 350px,
      rgba(122, 122, 122, 0.5) 370px
    );
  background-size: cover;
}

.absolute {
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
}

h1 {
  transform: translateY(30px);
}

#oneWithAn {
  transform: translateY(30px);
}

#button {
  transform: translateY(30px);
}

.font-space-mono {
  font-family: "Space Mono", monospace;
}
.font-exo {
  font-family: "Exo 2", sans-serif;
}

h2 {
  position: absolute;
}

.container {
  max-width: 990px;
  min-width: 320px;
  margin: 0 auto;
}
.projects {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-align: center;
}

.projects__wrapper {
  display: grid;
  grid-template-rows: repeat(1, minmax(auto, 500px));
  grid-template-columns: repeat(6, minmax(50px, 140px));
  column-gap: 5%;
  justify-content: center;
}
.projects__item {
  position: relative;
  display: block;
  transform: translateX(30px);
}

img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 1em;
}

h2 {
  position: relative;
  text-align: start;
  margin-bottom: 30px;
  transform: translateY(30px);
}
